:root {
  --orange-primary: #F96302;
  --orange-dark: #E55A02;
  --orange-light: rgba(249, 99, 2, 0.08);
  --orange-medium: rgba(249, 99, 2, 0.15);
  --white: #FFFFFF;
  --black: #000000;
  
  --glass-bg: rgba(255, 255, 255, 0.72);
  --glass-bg-heavy: rgba(255, 255, 255, 0.88);
  --glass-border: rgba(255, 255, 255, 0.5);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  --glass-shadow-hover: 0 12px 48px rgba(0, 0, 0, 0.12);
  
  --text-primary: #1d1d1f;
  --text-secondary: #6e6e73;
  --text-tertiary: #86868b;
  
  --positive: #30D158;
  --positive-bg: rgba(48, 209, 88, 0.12);
  --negative: #FF453A;
  --negative-bg: rgba(255, 69, 58, 0.12);
  --neutral: #8E8E93;
  --neutral-bg: rgba(142, 142, 147, 0.12);
  
  --background: #ffedde0b;
  --card-radius: 16px;
  --card-radius-lg: 24px;
  --transition: all 0.25s cubic-bezier(0.25, 0.1, 0.25, 1);
  --transition-fast: all 0.15s cubic-bezier(0.25, 0.1, 0.25, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'SF Pro Display', 'SF Pro Text', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: var(--background);
  min-height: 100vh;
  color: var(--text-primary);
  line-height: 1.47059;
  letter-spacing: -0.022em;
}

/* Loading Screen */
.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: var(--background);
}

.loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}

.loading-spinner {
  width: 32px;
  height: 32px;
  border: 2px solid var(--orange-light);
  border-top-color: var(--orange-primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-content p {
  font-size: 15px;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Glass Effects */
.glass {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  border-radius: var(--card-radius);
  box-shadow: var(--glass-shadow);
}

.glass-heavy {
  background: var(--glass-bg-heavy);
  backdrop-filter: blur(24px);
  -webkit-backdrop-filter: blur(24px);
  border: 1px solid var(--glass-border);
  border-radius: var(--card-radius);
  box-shadow: var(--glass-shadow);
}

/* Global Button Reset */
button {
  font-family: inherit;
  cursor: pointer;
  transition: var(--transition);
  border: none;
  background: none;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

/* Global Input Reset */
input, select {
  font-family: inherit;
  font-size: inherit;
}

select {
  cursor: pointer;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.12);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.2);
}